import gql from 'graphql-tag';

const getRandomInt = (max = 10) => {
  return ~~(Math.random()*max)
}

const getMockLessonProgress = () => {
  const days = 7
  let list = []
  let d = new Date(); 
  for (let i = 0; i< days; i++) {
    let date = new Date()
    date.setDate(d.getDate()-days+i)
    let dateStr = date.toLocaleDateString()
    list.push({
      name: dateStr.substr(0, dateStr.lastIndexOf("/")),
      lessonsPassed: getRandomInt(10)
    })
  }
  return list;
}

const getMockAnno = () => {
  let list = []
  for (let i = 0; i < 10; i++) {
      list.push({
          id: i,
          date: new Date(),
          message: "New model has been created " + i
      })
  }  
  return list;
}

const getMockGrades = () => {
  const gradeColors = ["#1b3a6b", "#2757a3", "#2e67c1", "#3376e0"] 
  return gradeColors.map((item, i) => {
    return {
      value: getRandomInt(),
      name: "Grade "+i,
      fill: item
    }
  })
}


export default (raFetchType, params) => {
  switch (raFetchType) {
    case 'GET_ONE':
      return {
        query: gql`query getCounts{
          allStudents {
             profile{
              id
              firstName
              lastName
            }
          }           
          allLessonModels{
            id
            name
          }    
        }`,
        variables: params,
        parseResponse: response => {              
          return {
            data: {
              id: 0,
              announcements: getMockAnno(),
              grades: getMockGrades(),
              lessonProgress: getMockLessonProgress(),
              students: response.data.allStudents,
              lessons: response.data.allLessonModels        
            }
          }
        }
      };
    default:
      return {};

  }

}
