import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case 'GET_ONE':
      return {
        query: gql`query classroom ($id: Int) {
          classroom(id: $id) {
            id
            name
            subject
            teacher{
              profile{
                id
              }
            }
            students{
              profile{
                id
              }
            }
            lessons{
              id
            }
          }
        }`,
        variables: params,
        parseResponse: response => {
          let { classroom } = response.data
          let students = classroom.students.map(item => item.profile.id)
          return {
            data: {
              id: classroom.id,
              name: classroom.name,
              teacher: classroom.teacher.profile.id,
              students: students
            }
          }
        }
      };
    case 'GET_LIST':
      return {
        query: gql`query allClassrooms {
          allClassrooms {
            id
            name
            subject
            teacher{
              profile{
                id
                firstName
                lastName
              }
            }
            students {
              profile{
                id
              }
            }        
            lessons{
              id
              name
            }
          }
        }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.allClassrooms,
            total: response.data.allClassrooms.length
          }
        }
      };
    case 'CREATE':
      return {
        query: gql`mutation createClassroom($classroom: ClassRoomInput) {
          createClassroom(classroom: $classroom) {
            ok
            classroom{
              id
              name
              subject
              teacher{
                profile{
                  id
                  firstName
                  lastName
                }
              }
              students{
                 profile{
                  id
                  firstName
                  lastName
                }
              }
              lessons{
                id
                name
              }
            }
          }
        }`,
        variables: {
          "classroom": params.data
        },
        parseResponse: response => {
          return {
            data: response.data.createClassroom.classroom
          }
        }
      };
    case 'UPDATE':
      return {
        query: gql`mutation updateClassroom($classroom: UpdateClassRoomInput) {
          updateClassroom(classroom: $classroom) {
            ok
            classroom{
              id
              name
              subject
              teacher{
                profile{
                  id
                  firstName
                  lastName
                }
              }
              students{
                 profile{
                  id
                  firstName
                  lastName
                }
              }
              lessons{
                id
                name
              }
            }
          }
        }`,
        variables: {
          "classroom": {
            id: params.data.id,
            lessons: params.data.lessons,
            name: params.data.name,
            students: params.data.students,
            teacher: params.data.teacher,
            subject: params.data.subject
          }
        },
        parseResponse: response => {
          return {
            data: response.data.updateClassroom.classroom
          }
        }
      };
    case "DELETE":
      return {
        query: gql`mutation deleteClassroom($id: ID!) {
            deleteClassroom(id: $id){
              ok
            }
          }`,
        variables: {
          "id": params.id
        },
        parseResponse: response => {
          return {
            data: {
              id: params.id,
              result: response.data.deleteClassroom.ok
            }
          };
        }
      };
    default:
      return {};
  }
}
