import React from 'react';
import {
  List,
  Datagrid,
  FormTab,
  TabbedForm,
  Edit,
  Create,
  TextField,
  DisabledInput,
  TextInput,
  LongTextInput,
  translate,
  ImageField  
} from 'react-admin';
import PolyModelView from '../../components/lessonModel/PolyModelView'
import { Field } from 'redux-form';
import Icon from '@material-ui/icons/Polymer';
import Typography from '@material-ui/core/Typography';

const required = (message = 'Required') => value => value ? undefined : message;

const LessonExpand = ({ record }) =>(
    <div>
      <Typography color="textSecondary" gutterBottom>Description</Typography>
      <Typography >{record.description}</Typography>   
    </div>
)

const LessonModelList = (props) => (
  <List  bulkActions={false} {...props} title={'resources.lessonModel.title'} >
    <Datagrid rowClick="edit" expand={<LessonExpand/>}>
      <TextField source="name" />      
      <ImageField source="thumbnail"  className="thumbNailView" label='resources.lessonModel.shape'/>
      <TextField source="code" />
    </Datagrid>
  </List>
);

const LessonModelTitle = translate(({ record, translate }) => (
  <span>
    {translate('resources.lessonModel.edit', { reference: record.name })}
  </span>
));

const LessonModelEdit = (props) => (
  <Edit title={<LessonModelTitle />} {...props}>
    <TabbedForm redirect="list">
      <FormTab label="info">
        <DisabledInput source="id" />
        <TextInput source="name" validate={required()} />
        <LongTextInput source="description" validate={required()} />
      </FormTab>
      <FormTab label="3dModel">
        <Field name="codeModel" component={PolyModelView} validate={required()} />
      </FormTab>
    </TabbedForm>
  </Edit>
);

const LessonModelCreate = (props) => {
  return <Create title="resources.lessonModel.create" {...props}>
    <TabbedForm redirect="list">
      <FormTab label="resources.info">
        <TextInput source="name" validate={required()} />
        <LongTextInput source="description" validate={required()  } />        
      </FormTab>
      <FormTab label="3dModel">
        <Field name="codeModel" component={PolyModelView} validate={required()} />
      </FormTab>
    </TabbedForm>
  </Create>
};

export default {
  icon: Icon,
  list: LessonModelList,
  edit: LessonModelEdit,
  create: LessonModelCreate
}
