import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case "GET_ONE":
      return {
        query: gql`query question ($id: Int) {
          question (id: $id) {
            id
            question
            model{
              id
              name
            }
          }
        }`,
        variables: params,
        parseResponse: response => {
          let { question } = response.data;
          return {
            data: {
              model: question.model.id,
              ...question
            }
          }
        }
      };
    case "GET_LIST":
      return {
        query: gql`query allQuestions {
          allQuestions {
            id
            question
            model{
              id
              name
            }
          }
        }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.allQuestions,
            total: response.data.allQuestions.length
          }
        }
      };
    case "GET_MANY": 
      return {
        query: gql`query allQuestions($idList: [ID]) {
            allQuestions(idList: $idList) {
              id
              question
              model{
                id
                name
              }
            }
          }`,
        variables: {
          idList: params.ids
        },
        parseResponse: response => {
          return {
            data: response.data.allQuestions,
            total: response.data.allQuestions.length
          }
        }
      };
     
    case "CREATE":
      return {
        query: gql`mutation createQuestion($newLessonquestion: LessonQuestionCreateGenericType!) {
          createLessonQuestion(newLessonquestion: $newLessonquestion) {
            ok
            lessonquestion{
              id
              question
              model{
                id
                name
              }
            }
            errors{
              field
              messages
            }
          }
        }`,
        variables: {
          newLessonquestion: {
            model: params.data.model,
            question: JSON.stringify(params.data.question)
          }
        },
        parseResponse: response => {
          let data = response.data.createLessonQuestion.lessonquestion;
          return {
            data: {
              model: data.model.id,
              ...data
            }
          }
        }
      };
    case "UPDATE":
      return {
        query: gql`mutation updateLessonQuestion($newLessonquestion: LessonQuestionUpdateGenericType!){
          updateLessonQuestion(newLessonquestion: $newLessonquestion){
            ok
            errors{
              messages
              field
            }
            lessonquestion{
              id
              question
              model{
                id
                name
              }
            }
          }
        }`,
        variables: {
          newLessonquestion: {
            id: params.data.id,
            model: params.data.model.id,
            question: JSON.stringify(params.data.question)
          }
        },
        parseResponse: response => {
          let data = response.data.updateLessonQuestion.lessonquestion;
          return {
            data: {
              model: data.model.id,
              ...data
            }
          }
        }
      };
    case "DELETE":
      return {
        query: gql`mutation deleteLessonQuestion($id: ID!) {
          deleteLessonQuestion(id: $id){
            ok
          }
        }`,
        variables: {
          "id": params.id
        },
        parseResponse: response => {
          return {
            data: {
              id: params.id,
              result: response.data.deleteLessonQuestion.ok
            }
          };
        }
      };
    default:
      return {};
  }
}
