import React from 'react';
import {
  List,
  Datagrid,
  SelectArrayInput,
  ReferenceInput,
  SelectInput,
  ReferenceArrayInput,
  Edit,
  FormTab,
  Create,
  TabbedForm,
  TextField,
  DisabledInput,
  TextInput,
  FunctionField
} from 'react-admin';
import Icon from '@material-ui/icons/School';

const required = (message = 'Required') =>
  value => value ? undefined : message;

const renderName = choice => `${choice.firstName} ${choice.lastName}`;

const ClassroomList = (props) => (
  <List bulkActions={false} {...props}>
    <Datagrid rowClick="edit">
      <TextField source="name" />
      <TextField label="resources.classroom.teacher" source="teacher.profile.firstName" />
      <TextField source="subject" />
      <FunctionField label="resources.classroom.numberStudents" render={record => record.students.length} />  
      <FunctionField label="resources.classroom.numberLessons" render={record => record.lessons.length} />  
    </Datagrid>
  </List>
);

const ClassroomTitle = ({ record }) => {
  return <span>Classroom {record.name}</span>;
};

const ClassroomEdit = (props) => {
  const renderProfileName = choice => choice ? `${choice.firstName} ${choice.lastName}` : "";
  
  return (<Edit title={<ClassroomTitle />} {...props} >
    <TabbedForm redirect="list">
      <FormTab label="resources.info">
        <DisabledInput source="id" />
        <TextInput source="name" validate={required()} />
        <ReferenceInput label="Teacher" source="teacher" reference="Teacher" validate={required()}>
          <SelectInput optionText={renderProfileName} optionValue="id" />
        </ReferenceInput>
      </FormTab>
      <FormTab label="students">
        <ReferenceArrayInput label="Students" source="students" reference="Student">
          <SelectArrayInput optionText={renderProfileName} optionValue="id" />
        </ReferenceArrayInput>
      </FormTab>
      <FormTab label="lessons">
        <ReferenceArrayInput label="resources.lesson.title" source="lessons" reference="LessonGroup">
          <SelectArrayInput optionText="name" optionValue="id" />
        </ReferenceArrayInput>
      </FormTab>
    </TabbedForm>
  </Edit>);
};

const ClassroomCreate = (props) => {
  return (<Create title="Create a Classroom" {...props} redirect="list">
    <TabbedForm redirect="list">
      <FormTab label="info">
        <TextInput source="name" validate={required()}/>
        <TextInput source="subject" validate={required()}/>
        <ReferenceInput label="Teacher" source="teacher" reference="Teacher" validate={required()}>
          <SelectInput optionText={renderName} optionValue="id" />
        </ReferenceInput>
      </FormTab>
      <FormTab label="students">
        <ReferenceArrayInput label="Students" source="students" reference="Student">
          <SelectArrayInput optionText={renderName} optionValue="id" />
        </ReferenceArrayInput>
      </FormTab>
      <FormTab label="lessons">
        <ReferenceArrayInput label="Lessons" source="lessons" reference="LessonGroup">
          <SelectArrayInput optionText="name" optionValue="id" />
        </ReferenceArrayInput>
      </FormTab>
    </TabbedForm>
  </Create>);
};

export default {
  icon: Icon,
  list: ClassroomList,
  edit: ClassroomEdit,
  create: ClassroomCreate
}
