import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case 'GET_ONE':
      return {
        query: gql`query teacher($id: Int ){
            teacher(id: $id) {
               profile{
                id
                email
                firstName
                lastName
              }
              classroomSet{
                id
                name
              }
            }
          }`,
        variables: params,
        parseResponse: response => {
          const { profile, classroomSet } = response.data.teacher
          const outData = {
            ...profile,
            classroomSet
          }
          return {
            data: outData
          }
        }
      }
    case 'GET_LIST':
      return {
        query: gql`query allTeachers{
              allTeachers {
                profile{
                  id
                  email
                  firstName
                  lastName
                }
                classroomSet{
                  id
                  name
                }
              }
            }`,
        variables: params,
        parseResponse: response => {
          let list = response.data.allTeachers.map(item => {
            return {
              ...item.profile,
              classroomSet: item.classroomSet
            }
          })
          return {
            data: list,
            total: list.length
          }
        }
      };
    case 'GET_MANY':
      return {
        query: gql`query allTeachers{
                allTeachers {
                  profile{
                    id
                    email
                    firstName
                    lastName
                  }
                  classroomSet{
                    id
                    name
                  }
                }
              }`,
        variables: params,
        parseResponse: response => {
          let list = response.data.allTeachers.filter(item => {
            return params.ids.indexOf(item.profile.id) !== -1;
          }).map(item => {
            return {
              ...item.profile,
              classroomSet: item.classroomSet
            }
          })
          return {
            data: list,
            total: list.length
          }
        }
      };
    case "UPDATE":
      return {
        query: gql`mutation updateTeacher($id: ID!, $teacher: UserUpdateInput){
                  updateTeacher(id: $id, profile: $teacher) {
                    teacher{
                      profile{
                        id
                        email
                        firstName
                        lastName
                      }
                    }
                  } 
                }`,
        variables: {
          "id": params.id,
          "teacher": {
            "email": params.data.email,
            "firstName": params.data.firstName,
            "lastName": params.data.lastName,
            "password": params.data.password,
          }
        },
        parseResponse: response => {
          console.log(response)
          return {
            data: response.data.updateStudent.student.profile
          };
        }
      };
    default:
      return {};
  }
}
