import React, { Component } from 'react';
import { GET_ONE, withDataProvider, Title, translate, Responsive } from 'react-admin';
import CardContent from '@material-ui/core/CardContent';
import compose from 'recompose/compose';
import { connect } from 'react-redux';
import CardList from "./dashboard/CardList"
import GradesChart from "./dashboard/GradesChart"
import LessonProgressChart from "./dashboard/LessonProgressChart"
import './dashboard.css';

class Dashboard extends Component {

  state = {}

  componentDidMount() {
    this.fetchCounter()
  }

  async fetchCounter() {
    const { dataProvider } = this.props;
    let { data } = await dataProvider(GET_ONE, "Dashboard")
    this.setState({
      ...data
    })
  }

  render() {
    const {
      announcements,
      lessons,
      students,
      grades,
      lessonProgress
    } = this.state;
    const { translate } = this.props;
    const announcementView = <CardList
      list={announcements}
      className="fixed-height"
      showValue={item => item.date.toLocaleString() + " - " + item.message}
      title="resources.dashboard.announcements" />
    return (
      <div>
        <Title title='resources.dashboard.title' />
        <CardContent>
          <Responsive
            small={
              <div className="flex-col">
                {announcementView}
                <CardList
                  className="fixed-height top-space"
                  title="resources.student.myStudents"
                  showValue={item => item.profile.firstName + " " + item.profile.lastName}
                  list={students} />
                <CardList
                  className="fixed-height top-space"
                  title="resources.lesson.myLessons"
                  list={lessons} />
                <GradesChart
                  data={grades}
                  className="top-space"
                  title={translate("resources.dashboard.grades")} />
                <LessonProgressChart
                  data={lessonProgress}
                  className="top-space"
                  title={translate("resources.dashboard.lessonProgress")} />
              </div>
            }
            medium={
              <div>
                {announcementView}
                <div className="flex-row">
                  <CardList
                    className="fixed-height left-col"
                    title="resources.student.myStudents"
                    showValue={item => item.profile.firstName + " " + item.profile.lastName}
                    list={students} />
                  <CardList
                    className="fixed-height right-col"
                    title="resources.lesson.myLessons"
                    list={lessons} />
                </div>
                <div className="flex-row">
                  <GradesChart
                    data={grades}
                    className="left-col"
                    title={translate("resources.dashboard.grades")} />
                  <LessonProgressChart
                    data={lessonProgress}
                    className="right-col"
                    title={translate("resources.dashboard.lessonProgress")} />
                </div>
              </div>
            }
          />
        </CardContent>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  version: state.admin.ui.viewVersion,
});

export default compose(
  connect(mapStateToProps),
  withDataProvider,
  translate
)(Dashboard);

