import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case "GET_ONE":
      return {
        query: gql`query student($id: Int ){
            student(id: $id) {
               profile{
                id
                email
                firstName
                lastName
              }
            }
          }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.student.profile
          }
        }
      };
    case "GET_MANY":
      return {
        query: gql`query allStudents{
                allStudents {
                   profile{
                    id
                    email
                    firstName
                    lastName
                  }
                }
              }`,
        variables: params,
        parseResponse: response => {
          let list = response.data.allStudents.filter(item => {
            return params.ids.indexOf(item.profile.id) !== -1;
          }).map(item => {
            return {
              ...item.profile
            }
          })
          return {
            data: list,
            total: list.length
          }
        }
      };
    case 'GET_LIST':

      return {
        query: gql`query allStudents{
                allStudents {
                   profile{
                    id
                    email
                    firstName
                    lastName
                  }
                }
              }`,
        variables: params,
        parseResponse: response => {
          let list = response.data.allStudents.map(item => item.profile)
          return {
            data: list,
            total: list.length
          }
        }
      };
    case "CREATE":
      return {
        query: gql`mutation createStudent($student: UserInput){
                createStudent(student: $student)  {
                  student{
                    profile{
                      id
                      email
                      firstName
                      lastName
                    }
                  }
                }
              }`,
        variables: {
          "student": params.data
        },
        parseResponse: response => {
          return {
            data: response.data.createStudent.student.profile
          };
        }
      };
    case "UPDATE":
      return {
        query: gql`mutation updateStudent($id: ID!, $student: UserUpdateInput){
                  updateStudent(id: $id, profile: $student) {
                    student{
                      profile{
                        id
                        email
                        firstName
                        lastName
                      }
                    }
                  } 
                }`,
        variables: {
          "id": params.id,
          "student": {
            "email": params.data.email,
            "firstName": params.data.firstName,
            "lastName": params.data.lastName,
            "password": params.data.password,
          }
        },
        parseResponse: response => {
          console.log(response)
          return {
            data: response.data.updateStudent.student.profile
          };
        }
      }
    case "DELETE":
      return {
        query: gql`mutation deleteStudent($id: ID!) {
                      deleteStudent(id: $id){
                        ok
                      }
                    }`,
        variables: {
          "id": params.id
        },
        parseResponse: response => {
          return {
            data: {
              id: params.id,
              result: response.data.deleteStudent.ok
            }
          };
        }
      };
    default:
      return {};
  }
}
