import { AUTH_LOGIN, AUTH_CHECK, AUTH_LOGOUT, AUTH_ERROR, AUTH_GET_PERMISSIONS } from 'react-admin';
import {getServerUrl} from './server'

export default (type, params) => {
  if (type === AUTH_CHECK) {
    return localStorage.getItem('token') ? Promise.resolve() : Promise.reject();
  }
  if (type === AUTH_ERROR) {
    console.log(params)
    return Promise.resolve();
  }

  if (type === AUTH_LOGOUT) {
    localStorage.removeItem('token');
    return Promise.resolve();
  }

  if (type === AUTH_LOGIN) {
    const { username, password } = params;
    console.log("LOGIN = "+getServerUrl())
    const request = new Request(getServerUrl()+'/api/token-auth/', {
      method: 'POST',
      body: JSON.stringify({ email: username, password }),
      headers: new Headers({ 'Content-Type': 'application/json' }),
    })
    return fetch(request)
      .then(response => {
        if (response.status < 200 || response.status >= 300) {
          throw new Error(response.statusText);
        }
        return response.json();
      })
      .then(({ token, role }) => {
        if (role) {
          localStorage.setItem("role", role)
        } else {
          if (username === "terence@superteam.io") {
            localStorage.setItem("role", "Admin")
          } else if (username === "jinoleynik@gmail.com") {
            localStorage.setItem("role", "Teacher")
          } else {
            localStorage.removeItem("role")
          }
        }
        localStorage.setItem('token', token);
      });
  }
  if (type === AUTH_GET_PERMISSIONS) {
    const role = localStorage.getItem('role');
    return role ? Promise.resolve(role) : Promise.reject();
  }
  return Promise.reject('Unknown method');
}
