import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import compose from 'recompose/compose';
import SettingsIcon from '@material-ui/icons/Settings';
import { withRouter } from 'react-router-dom';
import {
    translate,
    DashboardMenuItem,
    MenuItemLink,
    Responsive,
    WithPermissions
} from 'react-admin';

import SubMenu from './SubMenu';
import student from './students';
import teacher from './teachers';
import review from './reviews';
import classroom from './classrooms';
import question from './questions';
import lesson from './lessons';
import lessonModel from './lessonModels';
import lessonGroup from './lessonGroups';


class Menu extends Component {
    state = {
        menuCatalog: false,
        menuSales: false,
        menuCustomers: false,
    };

    static propTypes = {
        onMenuClick: PropTypes.func,
        logout: PropTypes.object,
    };

    handleToggle = menu => {
        this.setState(state => ({ [menu]: !state[menu] }));
    };

    render() {
        const { onMenuClick, open, logout, translate } = this.props;
        return (
            <div>
                <DashboardMenuItem onClick={onMenuClick} />
                <MenuItemLink
                    to={`/Classroom`}
                    primaryText={translate(`resources.classroom.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<classroom.icon />}
                    onClick={onMenuClick}
                />
                <WithPermissions>
                    {({ permissions }) => {
                        if (permissions === 'Admin') {
                            return (<SubMenu
                                handleToggle={() => this.handleToggle('menuUsers')}
                                isOpen={this.state.menuUsers}
                                sidebarIsOpen={open}
                                name="pos.menu.users"
                                icon={<student.icon />}
                            >
                                <MenuItemLink
                                    to={`/Student`}
                                    primaryText={translate(`resources.student.name`, {
                                        smart_count: 2,
                                    })}
                                    leftIcon={<student.icon />}
                                    onClick={onMenuClick}
                                />
                                <MenuItemLink
                                    to={`/Teacher`}
                                    primaryText={translate(`resources.teacher.name`, {
                                        smart_count: 2,
                                    })}
                                    leftIcon={<teacher.icon />}
                                    onClick={onMenuClick}
                                />
                            </SubMenu>);
                        } else if (permissions === 'Teacher') {
                            return <MenuItemLink
                                to={`/Student`}
                                primaryText={translate(`resources.student.myStudents`, {
                                    smart_count: 2,
                                })}
                                leftIcon={<student.icon />}
                                onClick={onMenuClick}
                            />;
                        } else {
                            return null;
                        }
                    }}
                </WithPermissions>


                <SubMenu
                    handleToggle={() => this.handleToggle('menuLessons')}
                    isOpen={this.state.menuLessons}
                    sidebarIsOpen={open}
                    name="pos.menu.lessons"
                    icon={<lesson.icon />}
                >
                    <MenuItemLink
                        to={`/LessonGroup`}
                        primaryText={translate(`resources.lessonGroup.name`, {
                            smart_count: 2,
                        })}
                        leftIcon={<lessonGroup.icon />}
                        onClick={onMenuClick}
                    />
                    <MenuItemLink
                        to={`/Lesson`}
                        primaryText={translate(`resources.lesson.name`, {
                            smart_count: 2,
                        })}
                        leftIcon={<lesson.icon />}
                        onClick={onMenuClick}
                    />
                    <MenuItemLink
                        to={`/Review`}
                        primaryText={translate(`resources.review.name`, {
                            smart_count: 2,
                        })}
                        leftIcon={<review.icon />}
                        onClick={onMenuClick}
                    />
                    <MenuItemLink
                        to={`/Question`}
                        primaryText={translate(`resources.question.name`, {
                            smart_count: 2,
                        })}
                        leftIcon={<question.icon />}
                        onClick={onMenuClick}
                    />
                </SubMenu>

                <MenuItemLink
                    to={`/LessonModel`}
                    primaryText={translate(`resources.lessonModel.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<lessonModel.icon />}
                    onClick={onMenuClick}
                />

                <MenuItemLink
                    to="/configuration"
                    primaryText={translate('pos.configuration')}
                    leftIcon={<SettingsIcon />}
                    onClick={onMenuClick}
                />
                <Responsive
                    small={logout}
                    medium={null} // Pass null to render nothing on larger devices
                />
            </div>
        );
    }
}

const mapStateToProps = state => ({
    open: state.admin.ui.sidebarOpen,
    theme: state.theme,
    locale: state.i18n.locale,
});

const enhance = compose(
    withRouter,
    connect(
        mapStateToProps,
        {}
    ),
    translate
);

export default enhance(Menu);
