import React from 'react'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { translate } from 'react-admin';
import compose from 'recompose/compose';
import ListSubheader from '@material-ui/core/ListSubheader';

const ListItems = ({ list = [], basePath, title, translate }) =>
    <List
        subheader={title && <ListSubheader component="div">{translate(title, list.length)}</ListSubheader>}
        component="nav">
        {
            list.map(item =>
                <ListItem key={"item_" + item.id} button component="a" href={`#/${basePath}/${item.id}`} >
                    <ListItemText primary={item.name} />
                </ListItem>
            )
        }
    </List>



const enhance = compose(
    translate
);

export default enhance(ListItems);

