import React, { Component } from 'react';
import { Admin, Resource } from 'react-admin';
import customRoutes from './routes';
import authProvider from './providers/authProvider';
import Login from './components/login';
import Dashboard from './components/dashboard';
import themeReducer from './themeReducer';
import englishMessages from './i18n/en';
import {buildDataProvider} from './providers/dataProvider'
import { Footer } from './components/Footer'

import {
  lessons,
  students,
  lessonModels,
  lessonGroups,
  questions,
  reviews,
  classrooms,
  teachers,
  AppLayout
} from './components';
import "./App.css"

const i18nProvider = locale => {
  if (locale === 'es') {
    return import('./i18n/es').then(messages => messages.default);
  }
  // Always fallback on english
  return englishMessages;
};


class App extends Component {

  constructor() {
    super();
    this.state = { dataProvider: null };
  }

  componentDidMount() {
    buildDataProvider().then(dataProvider => this.setState({ dataProvider }))
  }

  render() {
    const { dataProvider } = this.state;

    if (!dataProvider) {
      return <div>Loading</div>;
    }

    return (
      <React.Fragment>
        <Admin
          loginPage={Login}
          authProvider={authProvider}
          dataProvider={dataProvider}
          dashboard={Dashboard}
          customRoutes={customRoutes}
          i18nProvider={i18nProvider}
          locale="en"
          customReducers={{ theme: themeReducer }}
          appLayout={AppLayout}
        >
          <Resource name="Lesson" {...lessons} />
          <Resource name="Classroom" {...classrooms} />
          <Resource name="Student" {...students} />
          <Resource name="Teacher" {...teachers} />
          <Resource name="LessonModel"  {...lessonModels} />
          <Resource name="LessonGroup"  {...lessonGroups} />
          <Resource name="Question" {...questions} />
          <Resource name="Review" {...reviews} />
        </Admin>
        <Footer text={"Redhouse 2019"} />
      </React.Fragment>
    );
  }
}

export default App;
