import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case 'GET_ONE':
      return {
        query: gql`query lesson ($id: Int) {
          lesson(id: $id) {
            id
            model{
              id
              name
              code
              description
            }
            lesson
          }
        }`,
        variables: params,
        parseResponse: response => {
          let { lesson } = response.data;
          return {
            data: {
              model: lesson.model.id,
              ...lesson
            }
          }
        }
      };
    case 'GET_LIST':
      return {
        query: gql`query allLessons {
          allLessons {
            id
            model{
              id
              name
              code
              description
            }
            lesson
          }
        }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.allLessons,
            total: response.data.allLessons.length
          }
        }
      };
    case 'GET_MANY':
      return {
        query: gql`query allLessons {
          allLessons {
            id
            model{
              id
              name
              code
              description
            }
            lesson
          }
        }`,
        variables: params,
        parseResponse: response => {
          let list = response.data.allLessons.filter(item => {
            return params.ids.indexOf(item.id) !== -1;
          });
          return {
            data: list,
            total: list.length
          }
        }
      };
    case 'CREATE':
      return {
        query: gql`mutation createLesson($lesson: LessonCreateGenericType!) {
          createLesson(newLesson: $lesson) {
            ok
            lesson{
              id
              lesson
              model{
                id
                name
                code
                description
              }
            }         
          }
        }`,
        variables: {
          lesson: {
            model: params.data.model,
            lesson: JSON.stringify(params.data.lesson)
          }
        },
        parseResponse: response => {
          let data = response.data.createLesson.lesson;
          return {
            data: {
              model: data.model.id,
              ...data
            }
          }
        }
      };
    case 'UPDATE':
      return {
        query: gql`mutation updateLesson($lesson: LessonUpdateGenericType!) {
           updateLesson(newLesson: $lesson) {
            ok
            lesson{
              id
              lesson
              model{
                id
                name
                code
                description
              }
            }         
          }
        }`,
        variables: {
          lesson: {
            id: params.data.id,
            model: params.data.model.id,
            lesson: JSON.stringify(params.data.lesson)
          }
        },
        parseResponse: response => {
          let data = response.data.updateLesson.lesson;
          return {
            data: {
              model: data.model.id,
              ...data
            }
          }
        }
      };
    default:
      return {};
  };
}
