export const getTitleFromJson = (record, name) => {
    let object = record[name];
    let result = ""
    if (typeof object === "object") {
        result = object.title;
    } else {
        try {
            result = JSON.parse(object).title
        } catch (e) {
            console.log(object, 'error')
        }
    }
    return result;
}