import React from 'react';
import {
  List,
  Datagrid,
  Edit,
  Create,
  SimpleForm,
  TextField,
  DisabledInput,
  ReferenceInput,
  SelectInput,
  FunctionField
} from 'react-admin';
import Icon from '@material-ui/icons/LibraryBooks';
import { Field } from 'redux-form';
import RichModelEditor from '../../components/admin/RichModelEditor'
import {getTitleFromJson} from './Utils'

const required = (message = 'Required') => value => value ? undefined : message;

const LessonList = (props) => (
  <List {...props}>
    <Datagrid rowClick="edit">
      <TextField label="Model Name" source="model.name" />
      <FunctionField label="Lesson" render={record => getTitleFromJson(record, "lesson")} />
    </Datagrid>
  </List>
);

const LessonTitle = ({ record }) => <span>Lesson {record.title}</span>;

const LessonEdit = (props) => (
  <Edit title={<LessonTitle />} {...props}>
    <SimpleForm redirect="list">
      <DisabledInput source="id" />
      <ReferenceInput label="LessonModel" source="model.id" reference="LessonModel" validate={required()}>
        <SelectInput optionText="name" optionValue="id" />
      </ReferenceInput>
      <Field name="lesson" component={RichModelEditor} validate={required()} />
    </SimpleForm>
  </Edit>
);

const LessonCreate = (props) => (
  <Create title="Create a Lesson" {...props}>
    <SimpleForm redirect="list">
      <ReferenceInput label="LessonModel" source="model" reference="LessonModel" validate={required()}>
        <SelectInput optionText="name" optionValue="id" />
      </ReferenceInput>
      <Field name="lesson" component={RichModelEditor} validate={required()} />
    </SimpleForm>
  </Create>
);

export default {
  icon: Icon,
  list: LessonList,
  edit: LessonEdit,
  create: LessonCreate
}
