import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case 'GET_ONE':
      return {
        query: gql`query lessonGroup ($id: Int) {
        lessonGroup (id: $id) {
          id
          name
          lesson {
            id
            lesson
            model{
              id
              name
              code
              description
            }
          }
          question {
            id
            question
          }
          review {
            id
            review
          }
        }
      }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.lessonGroup
          }
        }
      }
    case 'GET_LIST':
      return {
        query: gql`query allLessonGroups {
        allLessonGroups {
          id
          name
          lesson{
            id
            model{
              id
              name
            }
          }
        }
      }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.allLessonGroups,
            total: response.data.allLessonGroups.length
          }
        }
      };
    case 'GET_MANY':
      return {
        query: gql`query allLessonGroups {
            allLessonGroups {
              id
              name
            }
          }`,
        variables: params,
        parseResponse: response => {
          let list = response.data.allLessonGroups.filter(item => {
            return params.ids.indexOf(item.id) !== -1;
          });
          return {
            data: list,
            total: list.length
          }
        }
      };
    case 'CREATE':
      return {
        query: gql`mutation createLessonGroup($newLessongroup: LessonGroupCreateGenericType!){
          createLessonGroup(newLessongroup: $newLessongroup) {
            ok
            errors{
              messages
            }
            lessongroup{
              id
              name              
            }
          }
        }`,
        variables: {
          newLessongroup: params.data
        },
        parseResponse: response => {
          return {
            data: response.data.createLessonGroup.lessongroup
          }
        }
      };
    case 'UPDATE':
      return {
        query: gql`mutation updateLessonGroup($newLessongroup: LessonGroupUpdateGenericType!){
            updateLessonGroup(newLessongroup: $newLessongroup) {
              ok
              errors{
                messages
              }
              lessongroup{
                id
                name         
              }
            }
          }`,
        variables: {
          newLessongroup: {
            id: params.data.id,
            
          } 
        },
        parseResponse: response => {
          return {
            data: response.data.updateLessonGroup.lessongroup
          }
        }
      };
    case "DELETE":
      return {
        query: gql`mutation deleteLessonGroup($id: ID!) {
              deleteLessonGroup(id: $id){
                ok
              }
            }`,
        variables: {
          "id": params.id
        },
        parseResponse: response => {
          return {
            data: {
              id: params.id,
              result: response.data.deleteLessonGroup.ok
            }
          };
        }
      };

    default:
      return {}
  }



}
