import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case "GET_ONE":
      return {
        query: gql`query review ($id: Int) {
          review (id: $id) {
            id
            review
            model{
              id
              name
            }
          }
        }`,
        variables: params,
        parseResponse: response => {
          let { review } = response.data;
          return {
            data: {
              model: review.model.id,
              ...review
            }
          }
        }
      };
    case 'GET_LIST':
      return {
        query: gql`query allReviews {
          allReviews {
            id
            review
            model{
              id
              name
            }
          }
        }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.allReviews || [],
            total: (response.data.allReviews || []).length
          }
        }
      };
    case "GET_MANY":
      return {
        query: gql`query allQuestions($idList: [ID]) {
                allQuestions(idList: $idList) {
                  id
                  question
                  model{
                    id
                    name
                  }
                }
              }`,
        variables: {
          idList: params.ids
        },
        parseResponse: response => {
          return {
            data: response.data.allQuestions,
            total: response.data.allQuestions.length
          }
        }
      };
    case "CREATE":
      return {
        query: gql`mutation createReview($newLessonreview: LessonReviewCreateGenericType!) {
          createLessonReview(newLessonreview: $newLessonreview) {
            ok
            lessonreview{
              id
              review
              model{
                id
                name
              }
            }
            errors{
              field
              messages
            }
          }
        }`,
        variables: {
          newLessonreview: {
            model: params.data.model,
            review: JSON.stringify(params.data.review)
          }
        },
        parseResponse: response => {
          let data = response.data.createLessonReview.lessonreview;
          return {
            data: {
              model: data.model.id,
              ...data
            }
          }
        }
      };
    case "UPDATE":
      return {
        query: gql`mutation updateLessonReview($newLessonreview: LessonReviewUpdateGenericType!){
            updateLessonReview(newLessonreview: $newLessonreview){
                ok
                errors{
                  messages
                  field
                }
                lessonreview{
                  id
                  review
                  model{
                    id
                    name
                  }
                }
              }
            }`,
        variables: {
          newLessonreview: {
            id: params.data.id,
            model: params.data.model.id,
            review: JSON.stringify(params.data.review)
          }
        },
        parseResponse: response => {
          let data = response.data.updateLessonReview.lessonreview;
          return {
            data: {
              model: data.model.id,
              ...data
            }
          }
        }
      };
    case "DELETE":
      return {
        query: gql`mutation deleteLessonReview($id: ID!) {
        deleteLessonReview(id: $id){
            ok
          }
        }`,
        variables: {
          "id": params.id
        },
        parseResponse: response => {
          return {
            data: {
              id: params.id,
              result: response.data.deleteLessonReview.ok
            }
          };
        }
      };
    default:
      return {};
  }
}
