import React, { Component } from 'react'
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import { translate } from 'react-admin';
import Button from '@material-ui/core/Button';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from '@material-ui/icons/Add';

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
  },
  columns: {
    display: 'flex',
    flexDirection: 'row',
  },
  column: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
  },
  flex: {
    display: 'flex'
  },
  flexItem: {
    flex: 1
  },
  flexButton: {
    alignSelf: 'center'
  }
};

class RichModelEditor extends Component {

  constructor(props) {
    super(props)
    let json = {}
    try {
      json = JSON.parse(props.input.value)
    } catch (e) { }
    this.state = {
      title: json.title || "",
      video: json.video || "",
      instructions: json.instructions || [""],
      choices: json.choices || [""],
      hints: json.hints || [""],
      answer: json.answer || ""
    }
  }

  onValueChanges = () => {
    const { onChange } = this.props.input
    onChange({ ...this.state })
    console.log(this.props.input)
  }

  render() {
    const { classes, withQuestions } = this.props
    return (
      <div className={classes.container}>
        <TextField
          label="Title"
          type="text"
          margin="normal"
          onBlur={this.onValueChanges}
          value={this.state.title}
          onChange={(e) => this.setState({ title: e.target.value })} />
        <TextField
          label="Video"
          type="text"
          margin="normal"
          onBlur={this.onValueChanges}
          value={this.state.video}
          onChange={(e) => this.setState({ video: e.target.value })} />
        {this.renderList("Instructions", "instructions")}
        {withQuestions && this.renderList("Hints", "hints")}
        {withQuestions && this.renderList("Choices", "choices")}
        {withQuestions && this.renderAnswer()}
      </div>
    )
  }

  renderList = (label, listName) => {
    const list = this.state[listName]
    const { classes } = this.props
    let views = list.map((item, i) => <div
      key={listName + "_" + i}
      className={classes.flex}>
      <TextField
        className={classes.flexItem}
        type="text"
        margin="normal"
        value={item}
        onBlur={this.onValueChanges}
        onChange={(e) => this.updateItem(listName, i, e.target.value)} />
      <IconButton
        key={listName + "_del_" + i}
        aria-label="Delete"
        className={classes.flexButton}
        onClick={e => this.removeItem(listName, i)}>
        <DeleteIcon />
      </IconButton>
    </div>)
    return <div className={classes.container}>
      <p>{label}</p>
      {views}
      <Button onClick={e => this.addItem(listName)} color="secondary" variant="outlined">
        <AddIcon />Add
      </Button>
    </div>
  }

  removeItem = (listName, i) => {
    let list = this.state[listName]
    list.splice(i, 1)
    let out = {}
    out[listName] = list
    this.setState(out)
  }

  updateItem = (listName, i, value) => {
    let list = this.state[listName]
    list[i] = value;
    let out = {}
    out[listName] = list
    this.setState(out)
  }

  addItem = (listName) => {
    let list = this.state[listName]
    list.push("")
    let out = {}
    out[listName] = list
    this.setState(out)
  }

  renderAnswer = () => {
    const { choices, answer } = this.state
    const { classes } = this.props
    let answerList = choices.filter(item => !!item)
    if (answerList.length > 0) {
      return <div className={classes.column}>
        <InputLabel>Answer</InputLabel>
        <Select
          value={answer}
          onChange={e => {
            this.setState({
              answer: e.target.value
            })
          }}
        >
          {answerList.map(item => {
            return <MenuItem key={item} value={item}>{item}</MenuItem>
          })}
        </Select>
      </div>
    } else {
      return null
    }

  }

}

const enhance = compose(
  withStyles(styles),
  translate
);

export default enhance(RichModelEditor); 
