import React, { Component } from 'react'
import * as THREE from 'three';
import { MTLLoader, OBJLoader, Box3, Group } from 'three-full';
import './PolyModelView.css';
const POLY_API_KEY = 'AIzaSyD6NmC_Gz35LViW1xPoGbTsvirDQTK2hMo'


export default class ModelView extends Component {

  constructor(props) {
    super(props);
    this.state = {}
    this.frameId = null;
    this.scene = new THREE.Scene();
    this.container = null;
    this.camera = null;
    this.renderer = null;
    this.isLoading = false;
  }

  componentDidMount() {
    const { width, height } = this.props;
    const viewer = document.getElementById('modelViewer');

    const WIDTH = width || 600;
    const HEIGHT = height || 600;

    this.camera = new THREE.PerspectiveCamera(60, WIDTH / HEIGHT, 0.01, 100);
    this.renderer = new THREE.WebGLRenderer();
    this.container = new Group();
    this.scene.add(this.container);
    this.camera.position.set(5, 3, 5);
    this.camera.lookAt(0, 1.5, 0);
    this.scene.background = new THREE.Color(0x222222);
    this.scene.add(new THREE.GridHelper(10, 10));

    const ambient = new THREE.HemisphereLight(0xbbbbff, 0x886666, 0.75);
    ambient.position.set(-0.5, 0.75, -1);
    this.scene.add(ambient);

    const light = new THREE.DirectionalLight(0xffffff, 0.75);
    light.position.set(1, 0.75, 0.5);
    this.scene.add(light);

    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(WIDTH, HEIGHT);
    viewer.appendChild(this.renderer.domElement);
    this.start();
    if (this.props.assetUrl) {
      this.loadAssetByCode(this.props.assetUrl)
    }
  }

  static getDerivedStateFromProps(props, state) {
    return {
      assetUrl: props.assetUrl,
      asset: props.asset
    };
  }

  render() {
    const { asset } = this.state;
    const { showInfo } = this.props
    return (
      <div>
        <div id="modelViewer"></div>
        {asset && showInfo && <div>
          <p>{asset.authorName}</p>
          <p>{asset.displayName}</p>
          <p>{asset.description}</p>
        </div>}
      </div>
    )
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (nextState.asset !== this.state.asset && nextProps.asset) {
      this.createObj(nextProps.asset)
    } else if (nextProps.assetUrl && (this.state.assetUrl !== nextProps.assetUrl)) {
      this.loadAssetByCode(nextProps.assetUrl)
    }
    return nextState.asset !== this.state.asset
      || (!!nextProps.assetUrl && (this.state.assetUrl !== nextProps.assetUrl));
  }

  loadAssetByCode = (value) => {
    let url = `https://poly.googleapis.com/v1/${value}?key=${POLY_API_KEY}`;
    fetch(url, {
      method: "GET",
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      }
    })
      .then(response => response.json())
      .then(asset => {
        if (asset) {
          this.createObj(asset)
          this.setState({
            asset: asset
          });
        }
      })
  }

  start = () => {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate);
    }
  }

  animate = () => {
    var time = performance.now() / 5000;
    this.camera.position.x = Math.sin(time) * 5;
    this.camera.position.z = Math.cos(time) * 5;
    this.camera.lookAt(0, 1.5, 0);
    this.renderer.render(this.scene, this.camera)
    this.frameId = requestAnimationFrame(this.animate)
  }

  createObj = (asset) => {
    const that = this;
    this.isLoading = true;
    var format = asset.formats.find(format => { return format.formatType === 'OBJ'; });
    var obj = format.root;
    var mtl = format.resources.find(resource => { return resource.url.endsWith('mtl') });
    var path = obj.url.slice(0, obj.url.indexOf(obj.relativePath));
    var loader = new MTLLoader();
    loader.setCrossOrigin(true);
    loader.setMaterialOptions({ ignoreZeroRGBs: true });
    loader.setResourcePath(path);
    loader.load(mtl.url, function (materials) {

      var loader = new OBJLoader();
      loader.setMaterials(materials);
      loader.load(obj.url, function (object) {

        var box = new Box3();
        box.setFromObject(object);

        // re-center
        var center = box.getCenter();
        center.y = box.min.y;
        object.position.sub(center);

        // scale
        var scaler = new Group();
        scaler.add(object);
        scaler.scale.setScalar(6 / box.getSize().length());
        const container = that.container
        while (container.children.length) {
          container.remove(container.children[0]);
        }
        container.add(scaler);
        that.isLoading = false;
      });
    });
  }
}
