import React from 'react';
import { List, Datagrid, Edit, Create, SimpleForm, TextField, EmailField, DisabledInput, TextInput, email, translate} from 'react-admin';
import Icon from '@material-ui/icons/SupervisorAccount';

const required = (message = 'Required') =>
  value => value ? undefined : message;

const StudentList = ({permissions, ...props}) => (
  <List bulkActions={false} {...props}>
    <Datagrid rowClick={permissions === "admin" ? "edit" : ""}> 
      <TextField source="firstName" />
      <TextField source="lastName" />
      <EmailField source="email" />
    </Datagrid>
  </List>
);

const StudentTitle = translate(({ record, translate }) => {
  let name = (record.firstName || "") + (" "+record.lastName || "") 
  return <span>
     {translate('resources.student.edit', { reference: name })}
  </span>;
});

const StudentEdit = (props) => (
  <Edit title={<StudentTitle />} {...props}>
    <SimpleForm redirect="list">
      <DisabledInput source="id" />
      <TextInput source="email" validate={email()} type="email" />
      <TextInput source="firstName" validate={required()} />
      <TextInput source="lastName" />
    </SimpleForm>
  </Edit>
);

const StudentCreate = (props) => (
  <Create title="Create a Student" {...props}>
    <SimpleForm redirect="list">
      <TextInput source="firstName" validate={required()} />
      <TextInput source="lastName" validate={required()} />
      <TextInput source="email" validate={email()} type="email" />
      <TextInput source="password" type="password" validate={required()} />
    </SimpleForm>
  </Create>
);

export default {
  icon: Icon,
  list: StudentList,
  edit: StudentEdit,
  create: StudentCreate
}
