import React from 'react';
import { FunctionField, ReferenceInput, SelectInput, List, Datagrid, Edit, Create, SimpleForm, TextField, DisabledInput } from 'react-admin';
import Icon from '@material-ui/icons/RateReview';
import { Field } from 'redux-form';
import RichModelEditor from '../../components/admin/RichModelEditor'
import {getTitleFromJson} from './Utils'

const required = (message = 'Required') =>
  value => value ? undefined : message;

const ReviewList = (props) => (
  <List {...props}>
    <Datagrid rowClick="edit">
      <TextField label="Model" source="model.name" />
      <FunctionField label="resources.review.label" render={record => getTitleFromJson(record, "review")} />
    </Datagrid>
  </List>
);

const ReviewEdit = (props) => (
  <Edit title="resources.review.edit" {...props}>
    <SimpleForm redirect="list">
      <DisabledInput source="id" />
      <ReferenceInput label="LessonModel" source="model.id" reference="LessonModel" validate={required()}>
        <SelectInput optionText="name" optionValue="id" />
      </ReferenceInput>
      <Field name="review" component={RichModelEditor} validate={required()} />
    </SimpleForm>
  </Edit>
);

const ReviewCreate = (props) => (
  <Create title="resources.review.create" {...props}>
    <SimpleForm redirect="list">
      <ReferenceInput label="LessonModel" source="model" reference="LessonModel" validate={required()}>
        <SelectInput optionText="name" optionValue="id" />
      </ReferenceInput>
      <Field name="review" component={RichModelEditor} validate={required()} />
    </SimpleForm>
  </Create>
);

export default {
  icon: Icon,
  list: ReviewList,
  edit: ReviewEdit,
  create: ReviewCreate
}
