import gql from 'graphql-tag';

export default (raFetchType, params) => {
  switch (raFetchType) {
    case 'GET_ONE':
      return {
        query: gql`query lessonModel ($id: Int) {
          lessonModel(id: $id) {
            id
            name
            code
            description
            thumbnail
          }
        }`,
        variables: params,
        parseResponse: response => {
          const {lessonModel} = response.data;
          return {
            data: {
              codeModel: {
                code: lessonModel.code,
                thumbnail: lessonModel.thumbnail
              },
              ...lessonModel
            }
          }
        }
      }
    case 'GET_LIST':
      return {
        query: gql`query allLessonModels {
            allLessonModels {
              id
              code
              name
              description
              thumbnail
            }
          }`,
        variables: params,
        parseResponse: response => {
          return {
            data: response.data.allLessonModels,
            total: response.data.allLessonModels.length
          }
        }
      };
    case 'GET_MANY':
      return {
        query: gql`query allLessonModels {
            allLessonModels {
              id
              code
              name
              description
            }
          }`,
        variables: params,
        parseResponse: response => {
          let list = response.data.allLessonModels.filter(item => params.ids.indexOf(item.id) !== -1)
          return {
            data: list,
            total: list.length
          }
        }
      };
    case 'CREATE':
      return {
        query: gql`mutation createLessonModel($newLessonModel: LessonModelCreateGenericType!){
              createLessonModel(newLessonmodel: $newLessonModel){
                 ok
                lessonmodel{
                  id
                  code
                  name
                  thumbnail
                  description      
                }
              }
            }`,
        variables: {
          "newLessonModel": {
            id:  params.data.id,
            name: params.data.name,
            description: params.data.description,
            code:  params.data.codeModel.code,
            thumbnail:  params.data.codeModel.thumbnail
          }
        },
        parseResponse: response => {
          return {
            data: response.data.createLessonModel.lessonmodel
          };
        }
      };
    case 'UPDATE':
      return {
        query: gql`mutation updateLessonModel($lessonModel: LessonModelUpdateGenericType!) {
          updateLessonModel(newLessonmodel: $lessonModel) {
            ok
            lessonmodel {
              id
              code
              name
              thumbnail
              description
            }
          }
        }`,
        variables: {
          "lessonModel": {
            id:  params.data.id,
            name: params.data.name,
            description: params.data.description,   
            code:  params.data.codeModel.code,
            thumbnail:  params.data.codeModel.thumbnail
          }
        },
        parseResponse: response => {
          return {
            data: response.data.updateLessonModel.lessonmodel
          };
        }
      };
    case "DELETE":
      return {
        query: gql`mutation deleteLessonModel($id: ID!) {
          deleteLessonModel(id: $id){
            ok
          }
        }`,
        variables: {
          "id": params.id
        },
        parseResponse: response => {
          return {
            data: {
              id: params.id,
              result: response.data.deleteLessonModel.ok
            }
          };
        }
      };
    default:
      return {};
  }
}
