import { ApolloClient } from 'apollo-client';
import { createHttpLink } from 'apollo-link-http';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { setContext } from 'apollo-link-context';
import queries from './queries';
import buildGraphQLProvider from 'ra-data-graphql';
import { getServerUrl } from './server'

const buildQuery = () => (raFetchType, resourceName, params) => {
    return queries[resourceName] ? queries[resourceName](raFetchType, params) : null
}

const authLink = setContext((_, { headers }) => {
    const token = localStorage.getItem('token');
    return {
        headers: {
            ...headers,
            authorization: token ? `Token ${token}` : "",
        }
    }
});
const client = new ApolloClient({
    link: authLink.concat(
        createHttpLink({ uri: getServerUrl() + "/graphql_private" })
    ),
    cache: new InMemoryCache(),
});

export const buildDataProvider = () => buildGraphQLProvider({ client, buildQuery, introspection: false })
