import React from 'react';

import {
  List,
  Datagrid,
  ReferenceInput,
  SelectInput,
  Edit,
  FormTab,
  Create,
  TabbedForm,
  TextField,
  TextInput,
  RichTextField,
  translate,
  SimpleShowLayout,
  ShowController,
  ShowView
} from 'react-admin';
import Icon from '@material-ui/icons/Extension'; 
import ModelView from '../../components/lessonModel/ModelView'

const required = (message = 'Required') => value => value ? undefined : message;

const LessonGroupList = (props) => (
  <List {...props} title={'resources.lessonGroup.title'}>
    <Datagrid rowClick="show">
      <TextField source="name" />
      <TextField source="lesson.model.name" />
    </Datagrid>
  </List>
);

const LessonGroupTitle = translate(({ record, translate }) => {
  return <span>
    {translate('resources.lesson.edit', { reference: record.name })}
  </span>;
});


const LessonGroupEdit = (props) => (
  <Edit title={<LessonGroupTitle />} {...props}>
    <TabbedForm redirect="list">
      <FormTab label="resources.lesson.label">
        <TextInput source="name" validate={required()} />
        <ReferenceInput label="Lessons" source="lesson.id" reference="Lesson">
          <SelectInput optionText="lesson" optionValue="id" />
        </ReferenceInput>
      </FormTab>
      <FormTab label="resources.question.label">
        <ReferenceInput label="resources.question.label" source="question.id" reference="Question">
          <SelectInput optionText="question" optionValue="id" />
        </ReferenceInput>
      </FormTab>
      <FormTab label="resources.review.label">
        <ReferenceInput label="resources.review.label" source="review.id" reference="Review">
          <SelectInput optionText="review" optionValue="id" />
        </ReferenceInput>
      </FormTab>
    </TabbedForm>
  </Edit>
);

const LessonGroupCreate = (props) => (
  <Create title="Create a Lesson Group" {...props}>
    <TabbedForm redirect="list">
      <FormTab label="resources.lesson.label">
        <TextInput source="name" validate={required()} />
        <ReferenceInput label="Lessons" source="lesson" reference="Lesson">
          <SelectInput optionText="lesson" optionValue="id" />
        </ReferenceInput>
      </FormTab>
      <FormTab label="resources.question.label">
        <ReferenceInput label="resources.question.label" source="question" reference="Question">
          <SelectInput optionText="question" optionValue="id" />
        </ReferenceInput>
      </FormTab>
      <FormTab label="resources.review.label">
        <ReferenceInput label="resources.review.label" source="review" reference="Review">
          <SelectInput optionText="review" optionValue="id" />
        </ReferenceInput>
      </FormTab>
    </TabbedForm>
  </Create>
);

export const ShowComponent = (props) => {
  return <ShowController {...props}>
    {controllerProps => {
      let code;
      if (controllerProps.record && controllerProps.record.lesson && controllerProps.record.lesson.model) {
        code = controllerProps.record.lesson.model.code
      }
      return <ShowView {...props} {...controllerProps}>
        <SimpleShowLayout>
          <TextField source="name" />
          <ModelView assetUrl={code} width={400} height={400} showInfo/>
          <TextField label="resources.modelName"  source="lesson.model.name"/>
          <TextField label="resources.description" source="lesson.model.description"/>
          <TextField label="resources.code" source="lesson.model.code"/>
          <RichTextField source="lesson.lesson" label="resources.lesson.label" />
          <RichTextField source="question.question" label="resources.question.label" />
          <RichTextField source="review.review" label="resources.review.label" />
        </SimpleShowLayout>
      </ShowView>
    }
    }
  </ShowController>
};

export default {
  icon: Icon,
  list: LessonGroupList,
  edit: LessonGroupEdit,
  create: LessonGroupCreate,
  show: ShowComponent
}
