import React from 'react'

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import { translate } from 'react-admin';
import compose from 'recompose/compose';
import { withStyles } from '@material-ui/core/styles';

import Card from '@material-ui/core/Card';

const styles = {
    rowGray: { backgroundColor: "rgba(100,100,100,0.2)"},
    headerWhite: {backgroundColor: "-internal-root-color"}
};

const CardList = ({ translate, classes, list, title, className, showValue = (value) => value.name }) => {

    return (
        <Card className={className}>
            <List
                subheader={<ListSubheader className={classes.headerWhite} component="div">{translate(title)}</ListSubheader>}
                component="nav">
                {
                    (list || []).map((item, i) =>
                        <ListItem key={title + "_" + i} className={i % 2 ? classes.rowGray : ""}>
                            <ListItemText primary={showValue(item)} />
                        </ListItem>
                    )
                }
            </List>
        </Card>
    )
}


const enhance = compose(
    withStyles(styles),
    translate
);

export default enhance(CardList); 