import React, { Component } from 'react'
import './PolyModelView.css';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import SearchIcon from '@material-ui/icons/Search';
import ModelView from './ModelView'

import MoreIcon from '@material-ui/icons/Cached';
const POLY_API_KEY = 'AIzaSyD6NmC_Gz35LViW1xPoGbTsvirDQTK2hMo'


export default class PolyModelView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            query: 'car',
            assets: []
        }
    }

    componentDidMount() {       
        const { value } = this.props.input
        if (value && value.code) {
            this.loadAssetByCode(value.code)
        }
    }

    render() {
        return (
            <div>
                <div className="search-inputs">
                    <p>Search poly model</p>
                    <Input
                        type="text"
                        value={this.state.query}
                        onChange={(e) => this.setState({ query: e.target.value })}
                    />
                    <Button type="submit" onClick={this.search} color="secondary" variant="contained">
                        <SearchIcon/>
                        Search
                    </Button>
                </div>
                {this.renderAssets()}
                <ModelView

                    asset={this.state.currentAsset}
                    showInfo/>
            </div>
        )
    }

    // authorName: "Alex SAFFY Safayan"
    // createTime: "2018-03-11T02:03:54.657143Z"
    // description: "A low poly storage ship scene modeled off of https://be.net/zika-A.M.S. - Alex Michael Safayan :)Follow me:https://www.instagram.com/newpxl/https://twitter.com/alexsafayanTags: #boat #ship #storage #water #river #road #car #truck #building #city"
    // displayName: "Storage Ship"
    // formats: (4) [{}, {}, {}, {}]
    // isCurated: true
    // license: "CREATIVE_COMMONS_BY"
    // name: "assets/bzRjbJ74JCr"
    // presentationParams: {orientingRotation: {}, colorSpace: "LINEAR", backgroundColor: "#01579b"}
    // thumbnail: {relativePath: "bzRjbJ74JCr.png", url: "https://lh3.googleusercontent.com/R_rOIi8aaB6q7q13gopjZE5cBOXmSmM860lyxLTACUZBCAgmCIBYyP3bDhfRWi_E", contentType: "image/png"}
    // updateTime: "2019-05-08T18:35:21.116871Z"
    // visibility: "PUBLIC"
    renderAssets = () => {
        const { assets, nextPageToken } = this.state
        let items = [];
        assets.forEach(item => {
            let image = <img
                onClick={this.onItemClick}
                data-id={item.name}
                className="poly-thumbnail"
                key={item.name}
                src={item.thumbnail.url}
                alt={item.displayName}
            />
            items.push(image)
        });
        if (nextPageToken) {
            const loadMore = <span key={"loadMore"} className="poly-thumbnail">
                <MoreIcon className="poly-thumbnail" onClick={this.loadMore} />
            </span>;
            items.push(loadMore)
        }
        if (items.length) {
            return (<div><hr /><span>{items}</span></div>)
        } else {
            return null
        }
    }

    loadMore = () => {
        const { query } = this.state
        this.loadPoly(query, true)
    }

    onItemClick = (e) => {
        const { assets } = this.state
        const { onChange } = this.props.input
        const dataId = e.target.getAttribute("data-id")
        console.log(e)
        if (assets && dataId) {
            let item = assets.find(item => item.name === dataId)
            if (item) {
                if (onChange) {
                    onChange({
                      code: item.code,
                      thumbnail: item.thumbnail.url
                    })
                }
                this.setState({
                    currentAsset: item
                })
            }
        }
    }

    loadAssetByCode = (value) => {
        let url = `https://poly.googleapis.com/v1/${value}?key=${POLY_API_KEY}`;
        fetch(url, {
            method: "GET",
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            }
        })
            .then(response => response.json())
            .then(asset => {
                console.log(asset)
                if (asset) {
                    // this.createObj(asset)
                    this.setState({
                        currentAsset: asset
                    });
                }
            })
    }

    search = (event) => {
        event.preventDefault();
        const { query } = this.state
        this.loadPoly(query, false)
    }

    //https://developers.google.com/poly/reference/api/rest/v1/assets/list
    loadPoly = (query, isNextPage = false) => {
        let url = `https://poly.googleapis.com/v1/assets?keywords=${query}&pageSize=20&format=OBJ&key=${POLY_API_KEY}`;
        if (isNextPage && this.state.nextPageToken) {
            url += "&pageToken=" + this.state.nextPageToken
        }

        fetch(url, {
            method: "GET",
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            }
        })
            .then(response => response.json())
            .then(json => {
                console.log(json)
                let assets = json.assets
                let currentAsset = this.state.currentAsset
                if (isNextPage) {
                    assets = [...this.state.assets, ...json.assets]
                } else {
                    currentAsset = null;
                }
                this.setState({
                    currentAsset: currentAsset,
                    assets: assets,
                    nextPageToken: json.nextPageToken,
                    totalSize: json.totalSize
                })
            })
    }  
}
